.main {
  min-height: 100vh;
  background-color: var(--color-beige-100);
  color: var(--color-blue-100);
  font-family: var(--font-sans);
  overflow: hidden;
  position: relative;
}
.main::selection {
  background-color: var(--color-cream-100);
  color: var(--color-white-100);
}
.absoluteContainer {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  overflow: hidden;
}
.expandedOverlay {
  position: fixed;
  inset: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  position: relative;
  height: 250vh;
  background-color: var(--color-beige-100);
}
.stickyWrapper {
  position: sticky;
  top: 0;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.contentWrapper {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.layer1 {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}
.floatingImage {
  position: absolute;
  aspect-ratio: 16 / 9;
  filter: brightness(0.6);
}
.floatingImageImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1440px) {
  .imgTop {
    width: 28rem !important;
  }
  .imgLeft {
    width: 28rem !important;
    left: -5% !important;
  }
  .imgRight {
    width: 28rem !important;
    right: -5% !important;
  }
  .imgBottomLeft {
    width: 28rem !important;
    left: -5% !important;
  }
  .imgBottomRight {
    width: 28rem !important;
    right: -5% !important;
  }
}
@media (max-width: 1280px) {
  .imgTop {
    width: 24rem !important;
  }
  .imgLeft {
    width: 20rem !important;
    left: 2% !important;
    top: 60% !important;
  }
  .imgRight {
    width: 20rem !important;
    right: 2% !important;
    top: 60% !important;
  }
  .imgBottomLeft {
    width: 22rem !important;
    left: 5% !important;
  }
  .imgBottomRight {
    width: 22rem !important;
    right: 5% !important;
  }
}
@media (max-width: 1024px) {
  .imgTop {
    width: 36rem !important;
    top: 0% !important;
  }
  .imgLeft {
    width: 32rem !important;
    left: -28% !important;
    top: 33% !important;
  }
  .imgRight {
    width: 32rem !important;
    right: -28% !important;
    top: 33% !important;
  }
  .imgBottomLeft {
    width: 36rem !important;
    left: -34% !important;
    bottom: 15% !important;
  }
  .imgBottomRight {
    width: 36rem !important;
    right: -34% !important;
    bottom: 15% !important;
  }
}
@media (max-width: 768px) {
  .imgTop {
    width: 16rem !important;
    top: -12% !important;
  }
}
@media (max-width: 640px) {
  .imgTop {
    width: 60% !important;
    top: -24% !important;
    aspect-ratio: 9 / 16 !important;
  }
  .imgLeft {
    display: block !important;
    width: 60% !important;
    left: -40% !important;
    top: 33% !important;
  }
  .imgRight {
    display: block !important;
    width: 60% !important;
    right: -40% !important;
    top: 33% !important;
  }
  .imgBottomLeft {
    display: block !important;
    width: 20% !important;
    left: 0% !important;
    bottom: 20% !important;
    aspect-ratio: 9 / 16 !important;
  }
  .imgBottomRight {
    display: block !important;
    width: 20% !important;
    right: 0% !important;
    bottom: 20% !important;
    aspect-ratio: 9 / 16 !important;
  }
}
.layer2 {
  position: absolute;
  z-index: 10;
  overflow: hidden;
  width: 100%;
  pointer-events: none;
  height: 100vh;
}
@media (min-width: 768px) {
  .layer2 {
    width: auto;
  }
}
.heroVideo {
  width: 100%;
  height: 100%;
  scale: 1.1;
  object-fit: cover;
}
@media (max-width: 768px) {
  .heroVideo {
    scale: 1.2;
  }
}
.layer3 {
  position: absolute;
  z-index: 30;
  width: 100%;
  max-width: 28rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(6px);
  border-width: 1px;
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.2);
  text-align: center;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  margin-left: 1rem;
  margin-right: 1rem;
}
@media (max-width: 640px) {
  .layer3 {
    max-width: 90%;
    padding: 1.5rem;
  }
}
.formTitle {
  font-family: var(--font-script);
  font-size: clamp(1.5rem, 1.25rem + 1.5vw, 2.5rem);
  color: var(--color-white-100);
  margin-bottom: 1rem;
}
.formSubtitle {
  font-family: var(--font-sans);
  color: var(--color-white-100);
  opacity: 0.9;
  margin-bottom: 2rem;
}
.privacyText {
  margin-top: 1.5rem;
  font-size: clamp(0.5rem, 0.45rem + 0.4vw, 0.75rem);
  color: var(--color-white-100);
  opacity: 0.5;
}
.footer {
  position: absolute;
  bottom: 1rem;
  width: 100%;
  text-align: center;
  z-index: 50;
  pointer-events: none;
}
.footerText {
  color: var(--color-beige-100);
  font-size: clamp(0.5rem, 0.45rem + 0.4vw, 0.75rem);
  font-family: var(--font-sans);
  opacity: 0.8;
}
